.modern-pagination {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 1rem;
  background: rgba(255, 255, 255, 0.04);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.15);
  border-radius: 1rem;
  padding: 1rem 2rem;
  width: 80%;
  max-width: 800px;
  margin: 2rem auto 0 auto;
  color: white;
  font-family: 'Segoe UI', sans-serif;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}

.modern-pagination .info {
  font-size: 1rem;
  opacity: 0.9;
  white-space: nowrap;
}

.modern-pagination .controls {
  display: flex;
  gap: 0.5rem;
  flex-wrap: wrap;
  justify-content: center;
}

.page-btn {
  background: rgba(255, 255, 255, 0.08);
  color: white;
  border: 1px solid rgba(255, 255, 255, 0.2);
  padding: 0.45rem 0.75rem;
  border-radius: 0.75rem;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.2s ease;
}

.page-btn:hover:not(:disabled) {
  background: rgba(255, 255, 255, 0.2);
  transform: scale(1.1);
}

.page-btn:disabled {
  opacity: 0.3;
  cursor: not-allowed;
}

.page-btn.active {
  background: white;
  color: #000;
  font-weight: bold;
  border: 1px solid black;
  box-shadow: 0 0 8px black;
}

.search-bar {
  display: flex;
  align-items: center;
  border: 2px solid white;
  border-radius: 999px;
  padding: 8px 16px;
  width: 300px;
  background-color: #111;
  margin: 0 auto;
  justify-content: center;
  align-items: center;
}

.search-bar input {
  flex: 1;
  border: none;
  background: transparent;
  color: white;
  outline: none;
  font-size: 16px;
}

.search-bar button {
  background: none;
  border: none;
  color: white;
  font-size: 18px;
  cursor: pointer;
}

.search-bar input::placeholder {
  color: rgba(255, 255, 255, 0.7);
}

.results-container {
  background-color: #111;
  color: white;
  min-height: 100vh;
  padding: 20px;
  font-family: sans-serif;
}

.results-container h2 {
  text-align: center;
  margin-bottom: 30px;

}

.results-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
  max-width: 800px;
  margin: 0 auto;

}

.result-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 2px solid white;
  padding: 12px 20px;
  background-color: #000;
  border-radius: 4px;
}

.result-item p {
  margin: 0;
  font-size: 16px;
}

.view-more {
  background: none;
  border: none;
  color: white;
  font-weight: bold;
  cursor: pointer;
  text-decoration: underline;
  font-size: 14px;
}

.view-more:hover {
  opacity: 0.8;
}

.result-item h3 {
  margin: 0;
  font-size: 16px;
  color: white;
}

.result-item p {
  margin: 4px 0 0;
  font-size: 14px;
  color: #ccc;
}

/* Ocupa 100% da tela */
.home-container {
  margin: 0;
  padding: 0;
  height: 100vh;
  width: 100vw;
  background-color: #111;
  color: #fff;
  display: flex;
  flex-direction: column;
}

/* Container que divide a tela em duas metades */
.fullscreen-split {
  flex: 1;
  display: flex;
}

/* Cada metade ocupa 50% da largura */
.half-section {
  flex: 1;
  height: 100%;
  background-color: #111;
  /* mesma cor para eliminar separação visível */
}

/* Resultado com estilo de card */
.result-card {
  position: relative;
  border: 1px solid #333;
  padding: 16px;
  background-color: #1a1a1a;
  border-radius: 8px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: transform 0.2s ease;
  height: 140px;
  /* ajuste conforme seu layout desejado */
  overflow: hidden;
  /* evita quebra inesperada se o conteúdo for maior */
}


.result-card:hover {
  transform: translateY(-2px);
  background-color: #222;
}

.result-card mark {
  background: linear-gradient(90deg, #004aad, #e385ec);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  color: transparent;

  /* Removendo o fundo amarelo padrão do <mark> */
  background-color: transparent;
  font-weight: bold;
}


.loading-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 50vh;
  color: white;
  font-family: sans-serif;
}

.spinner {
  width: 40px;
  height: 40px;
  border: 4px solid #ffffff;
  border-top: 4px solid transparent;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 12px;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.error-message {
  color: #ff4d4f;
  text-align: center;
  margin: 30px auto;
  font-size: 18px;
  max-width: 600px;
  font-weight: bold;
  background-color: #220000;
  padding: 16px;
  border-radius: 8px;
}

.search-wrapper {
  display: flex;
  justify-content: center;
  position: relative;
  z-index: 2;
  align-items: center;
  transition: margin-top 0.6s ease;
}

.search-wrapper.centered {
  margin-top: 20vh;
}

.search-wrapper.near-results {
  margin-top: -15rem;
}


.results-wrapper {
  position: relative;
  z-index: 1;
  padding: 2rem;
  margin-top: 0%;
  margin-bottom: 80px;
}

.results-container {
  max-width: 800px;
  margin: 0 auto;
  background-color: rgba(0, 0, 0, 0.6);
  border-radius: 50px;
  padding: 1.5rem;
  backdrop-filter: blur(8px);
  box-shadow: 0 0 20px black !important;
}

/* home-container não deve ter height: 100vh se os resultados forem grandes */
.home-container {
  position: relative;
  overflow-x: hidden;
  width: 100vw;
  min-height: 100vh;
  /* permite crescer conforme o conteúdo */
  background-color: #000;
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Garante que o fundo cobre tudo */
.starfield {
  position: fixed;
  /* fixo em vez de absolute para cobrir tudo mesmo com scroll */
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-image: url('./assets/2.png');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  z-index: -1;
}

/* Resultados ocupam largura total dentro do limite */
.results-container {
  width: 100%;
  max-width: 1200px;
  min-height: 10px;
  background-color: rgba(0, 0, 0, 0.75);
  padding: 40px 30px;
  border-radius: 16px;
  box-shadow: 0 0 10px #0ff;
}

/* Lista de resultados ocupa toda a largura */
.results-list {
  width: 90%;
}

.floating-astronaut {
  position: fixed;
  width: 200px;
  z-index: 2;
  top: 25%;
  left: 45%;
  transform: translate(-50%, -50%);
  pointer-events: none;
}

.results-container .view-more {
  position: absolute;
  top: 12px;
  right: 16px;
  background: none;
  border: none;
  font-weight: bold;
  cursor: pointer;
  font-size: 14px;
  text-decoration: none;
  display: flex;
  align-items: center;
  gap: 4px;
  transition: opacity 0.2s ease;

  /* Gradiente no texto */
  background: linear-gradient(90deg, #004aad, #e385ec);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  color: transparent;
}


.results-container .view-more:hover {
  opacity: 0.8;
  text-decoration: underline;
}

/* Ícone de seta externa (pode trocar por SVG depois) */
.external-icon {
  font-size: 14px;
}

.page-title {
  font-family: 'Orbitron', sans-serif;
  font-size: 3rem;
  text-align: center;
  margin-bottom: 0rem;
  margin-top: 2rem;
  transition: opacity 0.3s ease;

  /* Gradiente no texto */
  background: linear-gradient(90deg, #004aad, #e385ec);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  color: transparent;
}